import { ORDER_LIST } from '@/config/router.js'
	
export default {
	props: {
		orderList: {
			type: Array
		},
		isMinePage:{
			type: Boolean
		},
	},
	data() {
		return {

		};
	},
	methods: {
		changeNav: function(e){
			if(this.isMinePage){
				uni.navigateTo({
					url:`${ORDER_LIST}?state=${e.currentTarget.dataset.id}`
				})
			}
			this.$emit('changeOrderList', {id: e.currentTarget.dataset.id})
		}
	}
}
